import UniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import UniBackground from '@/components/uni-background/uni-background.vue';
import FunButton from '@/components/fun-button.vue';
export default {
	components:{
		UniNavBar,
		UniBackground,
		FunButton
	},
	data() {
		return {
			scroll:0,
			navButtons:{
				back:{
					type:'normal',
					text:'取消'
				}
			},
			imageLib:{
				more:'../../static/icons/more.png'
			},
			currCountType:0,
			currPayTime:0,
			countTypes:[
				'USDT','Forest','Xdag'
			],
			currTimes:[
				'15','20','30'
			]
		};
	},
	onPageScroll(val){
		this.scroll = val.scrollTop;
	},
	methods:{
		countTypeChange(e){
			this.currCountType = e.target.value;
		},
		payTimeChange(e){
			console.log(e);
			this.currPayTime = e.target.value;
		},
		message(){
			uni.navigateTo({
				url:'../message/message'
			})
		},
		link(url){
			uni.navigateTo({
				url
			})
		}
	}
}
